.aside {
    width: 462px;
    padding-left: 64px;
    border-right: 3px solid black;

    &__title {
        font-size: 27px;
        font-weight: 800;
    }
}

.hard-skills {
    padding-top: 37px;

    &__list {
        margin-top: 20px;
        list-style-type: none;
    }

    &__item {
        display: flex;
        align-items: center;
        gap: 10px;

        font-size: 22px;

        img {
            width: 30px;
        }
    }

    &__item+&__item {
        margin-top: 10px;
    }
}

.education {
    padding-top: 55px;

    &__university {
        margin-top: 20px;
        font-size: 27px;
        text-transform: uppercase;
    }

    &__years {
        margin-top: 10px;
        font-size: 22px;
    }

    &__level {
        margin-top: 25px;
        font-size: 27px;
        font-style: italic;
    }
}

.languages {
    padding-top: 55px;

    &__list {
        margin-top: 20px;
        margin-left: 40px;
    }

    &__item {
        font-size: 27px;

        span {
            font-size: 22px;
        }
    }

    &__item+&__item {
        margin-top: 7px;
    }
}

.contacts {
    padding-top: 55px;

    &__list {
        margin-top: 20px;
    }

    &__item {
        display: flex;
        align-items: center;
        gap: 10px;

        font-size: 22px;

        img {
            width: 30px;
        }
    }

    &__item+&__item {
        margin-top: 13px;
    }

    &__link {
        color: black;
    }
}