.header {
    padding: 70px 40px 37px 64px;
    border-bottom: 3px solid black;

    position: relative;

    &__name {
        font-size: 63px;
    }

    &__position {
        margin-top: 10px;
        font-size: 36px;
        font-weight: normal;
    }

    &__about-me-title {
        margin-top: 33px;
        font-size: 27px;
        font-weight: 800;
    }

    &__about-me-text {
        margin-top: 10px;
        line-height: 1.2;
        font-size: 23px;
        font-weight: 400;
        letter-spacing: -0.3px;
    }

    &__download-cv {
        position: absolute;
        top: 70px;
        right: 64px;
    }
}

.download-cv {
    padding: 20px;
    width: fit-content;
    display: block;


    background-color: black;
    border: 3px solid black;
    border-radius: 10px;

    color: white;
    font-size: 30px;
    text-decoration: none;

    transition: .3s all;
}

.download-cv:hover {
    color: black;
    background-color: white;
}