.main {
    padding-left: 40px;
    padding-bottom: 70px;
}

.title {
    font-size: 32px;
    font-weight: 800;

    sup {
        font-size: 16px;
        margin-left: 15px;
        font-weight: normal;
    }
}

.experience {
    padding-top: 37px;
}

.experience-item {
    margin-top: 27px;

    &__course {
        font-size: 27px;

        span {
            font-weight: 800;
        }
    }

    &__list {
        margin: 12px 0 0 40px;
    }

    &__list-item {
        font-size: 25px;
    }

    &__list-item+&__list-item {
        margin-top: 3px;
    }

    &__certificate-link {
        display: block;
        font-size: 22px;
        margin-top: 8px;
        color: black
    }
}

.project {
    padding-top: 37px;
}

.project-item {
    margin-top: 30px;
    display: grid;
    grid-template-columns: 137px 250px 400px;

    &__link {
        display: block;
        font-size: 25px;
        color: black;
    }

    &__link+&__link {
        margin-top: 14px;
    }

    &__title {
        font-size: 25px;
        font-weight: 800;
    }

    &__list {
        margin: 17px 0 0 42px;
    }

    &__list-item {
        font-size: 25px;
    }

    &__list-item+&__list-item {
        margin-top: 3px;
    }
}